/* ----------navbar responsive toggler------------ */

.dot, .dot:before, .dot:after {
    position: absolute;
    width: 10px;
    height: 10px;
    border-radius: 20px;
    background-color: #3a3c3e;
}

.dot {
    /* top: 50%;
    left: 50%; */
    margin-top: -10px;
    margin-left: -10px;
}

.dot:before, .dot:after {
    content: "";
}

.dot:before {
    right: 15px;
    transition: right .3s ease-out;
}

.dot:after {
    left: 15px;
    transition: left .3s ease-out;
}

.dots:hover .dot:before {
    right: -15px;
}

.dots:hover .dot:after {
    left: -15px;
}

